import React, { Component } from "react";
import { Link } from "react-router-dom";
import "./Index.css";
import marker from "./marker.svg";
import signpost from "./signpost.svg";
import train from "../layout/train.svg";
import { Range, getTrackBackground } from "react-range";

class Index extends Component {
  handleClick = e => {
    e.currentTarget.parentNode
      .querySelector(".locationList")
      .classList.toggle("closed");
  };

  render() {
    const {
      startPoint,
      endPoint,
      destinations,
      handleClose,
      handleClear,
      offset,
      handleRangeChange
    } = this.props;
    return (
      <React.Fragment>
        <div className="hero">
          <div className="heroContent">
            <h1 className="animated fadeIn">OnTrack</h1>
            <div className="graphicContainer">
              <ul>
                <li className="animated fadeInDown"></li>
                <li className="animated fadeInDown"></li>
                <li className="animated fadeInDown"></li>
              </ul>
              <img src={train} alt="" className="animated fadeInDown" />
              <ul>
                <li className="animated fadeInDown"></li>
                <li className="animated fadeInDown"></li>
                <li className="animated fadeInDown"></li>
              </ul>
            </div>
          </div>
        </div>
        <div className="controls">
          <div
            className="dropDownContainer animated fadeInUp"
            style={{ zIndex: 2, animationDelay: ".25s" }}
          >
            <div className="locationBox" onClick={this.handleClick}>
              <p>{startPoint.location}</p> <img src={marker} alt="" />
            </div>
            <ul className="locationList closed">
              {destinations.map(item => (
                <li
                  onClick={handleClose}
                  key={item.crs}
                  data-location={item.location}
                  data-crs={item.crs}
                  data-point="startPoint"
                >
                  <p>{item.location}</p>
                  <p>({item.crs})</p>
                </li>
              ))}
            </ul>
          </div>
          <div
            className="dropDownContainer animated  fadeInUp"
            style={{ zIndex: 1, animationDelay: ".35s" }}
          >
            <div className="locationBox" onClick={this.handleClick}>
              {" "}
              <p>{endPoint.location}</p> <img src={signpost} alt="" />
            </div>
            <ul className="locationList closed">
              {destinations.map(item => (
                <li
                  onClick={handleClose}
                  key={item.crs}
                  data-location={item.location}
                  data-crs={item.crs}
                  data-point="endPoint"
                >
                  <p>{item.location}</p>
                  <p>({item.crs})</p>
                </li>
              ))}
            </ul>
          </div>
          <div
            className="when animated fadeInUp"
            style={{ zIndex: 1, animationDelay: ".45s" }}
          >
            <label htmlFor="">When?</label>
            <Range
              step={1}
              min={0}
              max={120}
              values={offset}
              onChange={values => handleRangeChange(values)}
              renderTrack={({ props, children }) => (
                <div
                  {...props}
                  style={{
                    ...props.style,
                    height: "4px",
                    width: "100%",
                    background: getTrackBackground({
                      values: offset,
                      colors: ["#FF2897", "rgba(255,255,255,.15)"],
                      min: 0,
                      max: 120
                    })
                  }}
                >
                  {children}
                </div>
              )}
              renderThumb={({ props, isDragged }) => (
                <div
                  {...props}
                  style={{
                    ...props.style,
                    height: "20px",
                    width: "20px",
                    borderRadius: "100%",
                    backgroundColor: "#FFF",
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center"
                  }}
                >
                  <div
                    style={{
                      position: "absolute",
                      bottom: "-20px",
                      color: "#fff",
                      fontWeight: "bold",
                      fontSize: "14px"
                    }}
                  >
                    {offset[0] === 0 ? "Now" : offset[0] + "m"}
                  </div>
                </div>
              )}
            />
          </div>
          <div className="buttons">
            <button
              onClick={handleClear}
              className="animated fadeInUp"
              style={{ animationDelay: ".55s" }}
            >
              Clear
            </button>
            <Link
              to="/times"
              className="disabled animated fadeInUp"
              style={{ animationDelay: ".6s" }}
            >
              <button>Go</button>
            </Link>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Index;
